
.box-sticky-ctn {
  overflow: hidden;
  margin-bottom: 10px;
}
.box-sticky-ctn .title {
  font-size: 16px;
  font-weight: normal;
  line-height: 30px;
  margin-right: 20px;
  color: #333333;
}
.box-sticky-ctn .box-sticky {
  padding: 9px 20px 9px;
  border: none;
  background-color: #FFF;
  min-height: 48px;
  margin-left: 0;
  margin-right: 0;
  z-index: 1001;
  -webkit-box-shadow: 0px 0px 5px #333;
          box-shadow: 0px 0px 5px #333;
}
.box-sticky-ctn .box-sticky .box-filter-title {
  font-size: 16px;
  font-weight: normal;
  line-height: 32px;
  margin-right: 20px;
  color: #333333;
}
.sticky-anchor {
  font-size: 14px;
  display: inline-block;
  position: relative;
  padding: 5px 5px 2px 8px;
  text-decoration: none;
  background: transparent;
  color: #999;
}
.sticky-anchor.active {
  color: #333;
}
.sticky-anchor:after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  background-color: transparent;
  bottom: 0;
  left: 0;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.sticky-anchor::after {
  background-color: #F4786B;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  opacity: 0;
}
.sticky-anchor.active::after {
  opacity: 1;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.text-center {
  text-align: center;
}



